.section-part .part-icons{
    border: 1px solid purple;
    width: 50px;
    border-radius: 50%;
    height: 50px;
    display:  flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.section-part .part-icons .part-icon{
    font-size: 20px;
    color: purple;
}
@media(max-width:991px){
    .sections{
        flex-direction: column;
    }
}