.underline{
    width: 25%;
    height: 4px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: auto;
}
.short-part{
    width: 2%;
    height: 3px;
    background-color: blueviolet;
    border-radius: 5px;
}
.long-part{
    width: 10%;
    height: 3px;
    background-color: blueviolet;
    border-radius: 5px;
}
@media(max-width:991px){
    .underline{
        width: 50%;
    }
}