#scrolled{
    color: purple !important;
}
.nav{
    background-color: transparent;
    font-family:Arial, Helvetica, sans-serif;
}
.bg-background{
    background-color: white !important;
    color: black !important;
}
.li-color-black{
    color: black !important;
}
.li-color-black:hover{
    color: purple !important;
}
.nav .navbar-items li{
    color: white;
    font-size: 14px;
    font-weight: bold;
    transition: 0.5s all ease-in-out;
}
.nav .navbar-items li:hover{
    color: purple;
    transition: 0.5s all ease-in-out;
}
.icon-container{
    width: 100%;
    text-align: end;
    display: flex;
    flex-direction: row;
    justify-content: end;
}
.icon{
    width: 40px;
    height: 27px;
    cursor: pointer;
    display: none;
}
.icon-span-color{
    background-color: black !important;
}
.icon span{
    background-color: white;
    display: block;
    width: 30px;
    margin: auto;
    height: 3px;
    margin-bottom: 5px;
    margin-top: 2.5px;
}
@media(max-width:991px){
    .icon{display: block;}
    .nav .navbar-items{
        display: none;
        width: 90%;
        position: absolute;
        top: 45px;
        right: 0px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: white;
        box-shadow: 0px 2px 4px rgba(0,0,0,.3);
        border-radius: 5px;
        padding-bottom: 10px;
        transition: 1s all ease-in-out;
    }
    .nav .navbar-items li{
        color: rgb(54, 54, 54);
        margin-top: 5px;
        margin-bottom: 0px;
        font-size: 12px;
    }
}
@media(min-width:992px){
    .nav .navbar-items{
        display:flex !important;
}
}