.about{
    margin-top: 50px;
}
.about-parts{
    margin-top: 50px;
}
.button{
    width: 20%;
    margin: auto;
    padding: 13px;
    border:2px solid purple;
    text-align: center;
    border-radius: 5px;
    color: purple;
     font-size: 18px;
    cursor: pointer;
    transition: 0.5s all ease-in-out;
}
.button:hover{
    background-color: purple;
    color: white;
    transition: 0.5s all ease-in-out;
}
.part .part-icons{
  width: 120px;
  margin: auto;
  border-radius: 50%;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.part-icon{
    margin: auto;
    background-color: transparent;
    font-size: 35px;
    color: white;
}
.autoplay{
    position: relative;
    color: white;
}
.overly{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    opacity: 0.8;
}
.play{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: white;
    margin: auto;
    cursor: pointer;
}
.play .play-icon{
    color: purple;
    font-size: 35px;
}
.popup{
    position: absolute;
    top: -500px;
    width: 100%;
    height: 200vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,.8);
    transition: 0.5s;
}
.popup-vedio{
    position: relative;
}
@media(max-width:991px){
    .button{
        width: 40%;
    }
}